
body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
#app {
  width: 100%;
  height: 100%;
  /* Disable selection so it doesn't get annoying */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: moz-none;
  -ms-user-select: none;
  user-select: none;
}
#left {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0px;
  background: black;
  color: white;
  overflow: scroll;
  max-width: 70vw;
  min-width: 30vw;
}
#right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  background: white;
  min-width: 30vw;
  max-width: 70vw;
  overflow: scroll;
}
#handle {
  position: absolute;
  left: -4px;
  top: 0;
  width: 15px;
  cursor: ew-resize;
  background-color: gray;
  height: 5000px;
  z-index: 100;
}
#header {
  min-width: 28.5vw;
  max-width: 69vw;
  height: 20px;
  /* Safari for macOS & iOS */
  -webkit-backdrop-filter: blur(15px);
  /* Google Chrome */
  backdrop-filter: blur(15px);
  /* 设置背景半透明黑色 */
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  left: 0;
  right: 30vw;
  top: 0;
  padding: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 99;
